/*=======================================================
			Style Switcher
========================================================*/
.color-switcher {
	width: 266px;
	position: fixed;
	right: -266px;
	top: 150px;
	z-index: 999;
	padding: 0px;
	-webkit-transition: all 500ms ease;
	-moz-transition: all 500ms ease;
	-ms-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
	border-bottom-right-radius: 4px;
}
.color-switcher h5 {
	font-size: 15px;
	margin-top: 0;
	padding: 0 20px 0;
}
.color-switcher p {
	font-size: 16px;
	color: #000;
	line-height: 50px;
	margin: 0px;
	    margin-top: 0 !important;
    font-family: 'Playfair Display', serif;
}
.color-switcher ul {
	list-style: none;
	padding: 0;
}
.color-switcher ul li {
	float: left;
}
.color-switcher ul li a {
	display: block;
	width: 40px;
	height: 40px;
	outline: none;
	text-align: center;
	line-height: 40px;
	font-size: 14px;
	color: #333;
}
.color-switcher ul li a.blue {
	background: #428bca;
}
.color-switcher ul li a.green {
	background: #88c136;
}
.color-switcher ul li a.red {
	background: #e54242;
}
.color-switcher ul li a.yellow {
	background: #ff9c00;
}
.color-switcher ul li a.brown {
	background: #987654;
}
.color-switcher ul li a.cyan {
	background: #1ABC9C;
}
.color-switcher ul li a.purple {
	background: #c74a73;
}
.color-switcher ul li a.sky-blue {
	background: #00cdcd;
}
.color-switcher .gray {
	background: #656565;
}
/*=======================================================
			FLAT COLORS
========================================================*/
.color-switcher .peter-river {
	background: #3498DB;
}
.color-switcher .nephritis {
	background: #27AE60;
}
.color-switcher .amethyst {
	background: #9B59B6;
}
.color-switcher .wet-asphalt {
	background: #34495E;
}
.color-switcher .night-shade {
	background: #2C3E50;
}
.color-switcher .sun-flowers {
	background: #F1C40F;
}
.color-switcher .orange {
	background: #F39C12;
}
.color-switcher .carrot {
	background: #E67E22;
}
.color-switcher .alizarin {
	background: #E74C3C;
}
.color-switcher .sliver {
	background: #BDC3C7;
}
.color-switcher .green-sea {
	background: #16A085;
}
.color-switcher .black {
	background: #000;
	display: none;
}
.color-switcher .white {
	background: #fff;
	display: none;
}
.color-switcher .light {
	background: #fff;
	width: 60px;
	color: #000;
	text-align: center;
	float: left;
	border: 1px solid #666;
	margin-top: 15px;
	margin-right: 22px;
}
.color-switcher .dark {
	background: #272727;
	width: 60px;
	float: right;
	color: #fff;
	border: 1px solid #666;
	margin-top: 15px;
}
.picker_close {
	width: 60px;
	height: 60px;
	position: absolute;
	left: -60px;
	top: 0px;
	z-index: -1;
	box-shadow: 0 0 10px rgba(0,0,0,0.2);
	text-align: center;
	background: #fff;
	border-bottom-left-radius: 4px;
	border-top-left-radius: 4px;
}
.picker_close i {
	font-size: 30px;
	line-height: 60px;
}
.position {
	right: 0;
	-webkit-transition: all 500ms ease;
	-moz-transition: all 500ms ease;
	-ms-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
}
.theme-colours, .layouts, .backgrounds, .choose-header, .choose-footer {
	display: table;
	text-align: left;
	padding-top: 0px;
	width: 100%;
	padding: 18px;
	background: #fff;
	padding-top: 0px;
}
.layouts a {
	display: inline-block;
	text-align: center;
	width: 50%;
	float: left;
	font-size: 10px;
	color: #000;
	margin-bottom: 20px;
	border: 1px solid #e7e7e7;
	padding: 10px 15px;
}
.layouts a:hover {
	background: #000;
	color: #fff;
	border-color: #000;
}
