/*=======================================================
			Style Switcher
========================================================*/
.color-switcher {
	width: 266px;
	position: fixed;
	right: -266px;
	top: 150px;
	z-index: 999;
	padding: 0px;
	transition: all 500ms ease;
	border-bottom-right-radius: 4px;
}
.color-switcher h5 {
	font-size: 15px;
	margin-top: 0;
	padding: 0 20px 0;
}
.color-switcher p {
	font-size: 16px;
	color: #000;
	line-height: 50px;
	margin: 0px;
	    margin-top: 0 !important;
    font-family: 'Playfair Display', serif;
}
.color-switcher ul {
	list-style: none;
	padding: 0;
}
.color-switcher ul li {
	float: left;
}
.color-switcher ul li a {
	display: block;
	width: 40px;
	height: 40px;
	outline: none;
	text-align: center;
	line-height: 40px;
	font-size: 14px;
	color: #333;
}
.color-switcher ul li a.blue {
	background: #428bca;
}
.color-switcher ul li a.green {
	background: #88c136;
}
.color-switcher ul li a.red {
	background: #e54242;
}
.color-switcher ul li a.yellow {
	background: #ff9c00;
}
.color-switcher ul li a.brown {
	background: #987654;
}
.color-switcher ul li a.cyan {
	background: #1ABC9C;
}
.color-switcher ul li a.purple {
	background: #c74a73;
}
.color-switcher ul li a.sky-blue {
	background: #00cdcd;
}
.color-switcher .gray {
	background: #656565;
}
/*=======================================================
			FLAT COLORS
========================================================*/
.color-switcher .peter-river {
	background: #3498DB;
}
.color-switcher .nephritis {
	background: #27AE60;
}
.color-switcher .amethyst {
	background: #9B59B6;
}
.color-switcher .wet-asphalt {
	background: #34495E;
}
.color-switcher .night-shade {
	background: #2C3E50;
}
.color-switcher .sun-flowers {
	background: #F1C40F;
}
.color-switcher .orange {
	background: #F39C12;
}
.color-switcher .carrot {
	background: #E67E22;
}
.color-switcher .alizarin {
	background: #E74C3C;
}
.color-switcher .sliver {
	background: #BDC3C7;
}
.color-switcher .green-sea {
