/*=======================================================
      THEME STYLING START
========================================================*/
* {
	margin: 0px;
	padding: 0px;
}
body {
	background: #FFF;
	font-weight: normal;
	position: relative;
	font-size: 14px;
	font-family: 'Raleway', sans-serif;
}
/*=======================================================
      WRAPPER
========================================================*/
#wrap {
	position: relative;
	width: 100%;
	overflow: hidden;
}
/*=======================================================
      HEADINGS
========================================================*/
h1, h2, h3, h4, h5, h6 {
	color: #262626;
	font-weight: bold;
	text-transform: uppercase;
	font-family: 'Raleway', sans-serif;
}
h1 {
	font-size: 48px;
}
h2 {
	font-size: 40px;
	font-weight: bold;
}
h3 {
	font-size: 36px;
}
h4 {
	font-size: 30px;
}
h5 {
	font-size: 18px;
	padding: 20px 0;
	margin: 0px;
	color: #333333;
	letter-spacing: 1px;
}
h6 {
	font-size: 16px;
}
p {
	color: #333333;
	font-size: 13px;
	line-height: 20px;
	letter-spacing: 0.5px;
	text-rendering: optimizeLegibility;
}
a {
	transition: all 0.4s ease-in-out;
	text-decoration: none !important;
}
img {
	transition: 0.4s ease-in-out;
	display: inline-block !important;
}
ul {
	margin-bottom: 0px;
}
li {
	list-style: none;
}
a {
	text-decoration: none;
	color: #141414;
}
.position-center-center {
	left: 50%;
	position: absolute;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}
/*=======================================================
      SECTION
========================================================*/
section {
	position: relative;
	width: 100%;
	background: #fff;
}
.resume {
	background: #f5f5f5;
	padding: 100px 0;
}
h5.tittle {
	font-size: 18px;
	padding: 20px;
	margin: 0px;
	color: #333333;
	letter-spacing: 1px;
	border-bottom: 1px solid #e6e6e6;
}
/*=======================================================
      TOP HEAD
========================================================*/
.top-head {
	margin-bottom: 30px;
}
.top-head h4 {
	text-transform: uppercase;
	font-weight: 800;
	margin-right: 20px;
	float: left;
	margin: 0px;
	line-height: 38px;
	margin-right: 30px;
	display: inline-block;
}
.top-head a {
	letter-spacing: 1px;
	background: #346abb;
	color: #fff;
	padding: 10px 20px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 12px;
	margin-top: 3px;
	display: inline-block;
}
.top-head a:hover {
	background: #333;
}
/*=======================================================
      SIDE BAR
========================================================*/
.side-bar {
	background: #fff;
}
.side-bar li {
}
.side-bar .personal-info {
	padding: 25px;
	border-bottom: 1px solid #e6e6e6;
}
.side-bar .personal-info li span {
	width: 50%;
	float: left;
	text-transform: uppercase;
	font-weight: bold;
}
.side-bar .personal-info li p {
	margin: 0px;
	line-height: 30px;
}
.side-bar .personal-info li a {
	color: #346abb;
}
.bor-btm {
	border-bottom: 1px solid #e6e6e6;
}
