	content: "\f071";
	font-family: 'FontAwesome';
	position: absolute;
	color: red;
}
.success-msg {
	background: #fff;
	border: 1px solid #ABD0A8;
	color: #589051;
	margin-bottom: 30px;
	width: 100%;
	display: inline-block;
	text-align: center;
	padding: 11px 16px;
	border-radius: 4px;
	display: none;
}
.success-msg i {
	padding: 10px;
	border-radius: 50%;
	border: 1px solid #ABD0A8;
	margin-right: 10px;
}
.contact form li {
	text-align: left;
	margin-bottom: 10px;
	display: inline-block;
	width: 100%;
}
.contact form li input {
	border: none;
	height: 40px;
	width: 100%;
	border-radius: 0px;
	line-height: 30px;
	color: #333;
	box-shadow: none;
	padding: 0px 20px;
	font-size: 13px;
	background: #f5f5f5;
	text-transform: none;
	transition: all 0.4s ease-in-out;
}
.contact form li textarea {
	height: 120px !important;
	border: none;
	color: #333;
	box-shadow: none;
	padding: 10px 20px;
	font-size: 13px;
	background: #f5f5f5;
	text-transform: none;
	transition: all 0.4s ease-in-out;
}
.contact form li .btn {
	text-transform: uppercase;
}
.contact-form .tittle {
	text-align: center;
}
.contact-form .tittle hr {
	width: 28px;
	height: 5px;
	margin: 15px auto;
	background: #448aff;
	border: none;
}
.contact-form .tittle p {
	text-transform: none;
	font-size: 16px;
}
.contact-form label {
	font-weight: normal;
	display: inline-block;
	width: 100%;
	margin-bottom: 10px;
	color: #000;
}
.contact-form label span {
	color: #fff;
}
.contact-form textarea {
	display: inline-block;
	width: 100%;
	position: relative;
	padding: 10px 20px;
}
.contact-form button {
	border: none;
	transition: all 0.4s ease-in-out;
	letter-spacing: 1px;
	background: #346abb;
	color: #fff;
	padding: 15px 30px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 12px;
	display: inline-block;
}
.contact-form button:hover {
	background: #333;
	color: #fff;
	border-color: #fff;
}
.inside-sec {
	width: 100%;
	display: inline-block;
	background: #fff;
}
.bio-info p {
	line-height: 24px;
	letter-spacing: 0px;
}
.skills h6 {
	font-size: 12px;
	letter-spacing: 0.5px;
	font-weight: 700;
	margin-bottom: 20px;
}
/*=======================================================
			ACCORDION
========================================================*/
.accordion .panel-default .panel-heading .panel-title a:before {
	content: "\f208";
	font-family: "Ionicons";
	padding: 0px;
	color: #fff;
	height: 30px;
	width: 30px;
	background: #346abb;
	border: 1px solid #346abb;
	text-align: center;
	top: 0px;
	left: 15px;
	line-height: 29px;
	position: absolute;
	font-size: 12px;
}
.accordion .panel-default .panel-heading .panel-title a.collapsed:before {
